<template>
  <div class="sku chartPage" @click="closeByDialog">
    <AppHeader>
      <p slot="center">SKU统计</p>
    </AppHeader>

    <van-tabs v-model="active" animated sticky :offset-top="offsetTop" @click="tabOnClick">
      <van-tab title="SKU">
        <div class="filters">
          <div class="date-filters">
            <input type="text" @click.stop="openByDialog" :value="calendar.display" readonly />
          </div>
          <transition name="fade">
            <div class="calendar-dialog" v-if="calendar.show">
              <div class="calendar-dialog-mask" @click="closeByDialog"></div>
              <div class="calendar-dialog-body">
                <calendar
                  :range="calendar.range"
                  :zero="calendar.zero"
                  :lunar="calendar.lunar"
                  :value="calendar.value"
                  @select="calendar.select"
                ></calendar>
              </div>
            </div>
          </transition>
        </div>

        <div class="myChart-box">
          <div class="myChart" ref="myChart_sku" style="width: 375px;height: 360px"></div>
        </div>
      </van-tab>
      <van-tab title="季节性SKU">
        <div class="filters">
          <div class="date-filters">
            <input type="text" @click.stop="openByDialog" :value="calendar2.display" readonly />
          </div>
          <transition name="fade">
            <div class="calendar-dialog" v-if="calendar2.show">
              <div class="calendar-dialog-mask" @click="closeByDialog"></div>
              <div class="calendar-dialog-body">
                <calendar
                  :range="calendar2.range"
                  :zero="calendar2.zero"
                  :lunar="calendar2.lunar"
                  :value="calendar2.value"
                  @select="calendar2.select"
                ></calendar>
              </div>
            </div>
          </transition>
        </div>

        <div class="myChart-box">
          <div class="myChart" ref="myChart_jjxSku" style="width: 375px;height: 360px"></div>
        </div>
      </van-tab>
      <van-tab title="移入移出SKU">
        <div class="filters">
          <div class="date-filters">
            <input type="text" @click.stop="openByDialog" :value="calendar3.display" readonly />
          </div>
          <transition name="fade">
            <div class="calendar-dialog" v-if="calendar3.show">
              <div class="calendar-dialog-mask" @click="closeByDialog"></div>
              <div class="calendar-dialog-body">
                <calendar
                  :range="calendar3.range"
                  :zero="calendar3.zero"
                  :lunar="calendar3.lunar"
                  :value="calendar3.value"
                  @select="calendar3.select"
                ></calendar>
              </div>
            </div>
          </transition>
        </div>

        <div class="myChart-box">
          <div class="myChart" ref="myChart_yrycSku" style="width: 375px;height: 360px"></div>
        </div>
      </van-tab>
    </van-tabs>
  </div>
</template>

<script>
import Vue from "vue";
import { Tab, Tabs, Calendar, Field } from "vant";

Vue.use(Tab)
  .use(Tabs)
  .use(Calendar)
  .use(Field);

import AppHeader from "@/components/AppHeader.vue";
import calendar from "@/components/calendar/calendar.vue";

import echarts from "echarts/lib/echarts"; // 引入基本模板
import line from "echarts/lib/chart/line"; // 引入线状图组件
import legend from "echarts/lib/component/legend"; // 引入legend组件
import tooltip from "echarts/lib/component/tooltip"; // 引入tooltip组件

export default {
  name: "Sku",
  components: {
    AppHeader,
    calendar
  },

  data() {
    return {
      active: 0,
      screenWidth: document.body.clientWidth, // 屏幕尺寸
      offsetTop: 90 / (750 / document.body.clientWidth),

      calendar: {
        display: "2020/02/16 ~ 2020/03/16", //输入框站位日期
        show: false, //是否显示
        range: true,
        zero: true,
        value: [
          [2018, 2, 16],
          [2019, 2, 16]
        ], //默认选中日期
        lunar: true, //显示农历
        select: (begin, end) => {
          // console.log(begin, end);
          this.calendar_startDate = begin.join("-");
          this.calendar_endDate = end.join("-");
          this.calendar.show = false;
          this.calendar.value = [begin, end];
          this.calendar.display = begin.join("/") + " ~ " + end.join("/");
          this.$nextTick(() => {
            this.get_SkuChart();
          });
        }
      },
      calendar2: {
        display: "2020/02/16 ~ 2020/03/16", //输入框站位日期
        show: false, //是否显示
        range: true,
        zero: true,
        value: [
          [2018, 2, 16],
          [2019, 2, 16]
        ], //默认选中日期
        lunar: true, //显示农历
        select: (begin, end) => {
          // console.log(begin, end);
          this.calendar2_startDate = begin.join("-");
          this.calendar2_endDate = end.join("-");
          this.calendar2.show = false;
          this.calendar2.value = [begin, end];
          this.calendar2.display = begin.join("/") + " ~ " + end.join("/");
          this.$nextTick(() => {
            this.get_jjxSkuChart();
          });
        }
      },
      calendar3: {
        display: "2020/02/16 ~ 2020/03/16", //输入框站位日期
        show: false, //是否显示
        range: true,
        zero: true,
        value: [
          [2018, 2, 16],
          [2019, 2, 16]
        ], //默认选中日期
        lunar: true, //显示农历
        select: (begin, end) => {
          // console.log(begin, end);
          this.calendar3_startDate = begin.join("-");
          this.calendar3_endDate = end.join("-");
          this.calendar3.show = false;
          this.calendar3.value = [begin, end];
          this.calendar3.display = begin.join("/") + " ~ " + end.join("/");
          this.$nextTick(() => {
            this.get_yrycSkuChart();
          });
        }
      },
      calendar_startDate: "",
      calendar_endDate: "",
      calendar2_startDate: "",
      calendar2_endDate: "",
      calendar3_startDate: "",
      calendar3_endDate: "",
      getStoreInfo: "",
      sku_myChart: null,
      resultData: []
    };
  },
  created() {
    this.get_defaultData();
    this.get_data();
  },
  mounted() {},
  methods: {
    tabOnClick(name, title, index) {
      if (name == 0) {
        this.$nextTick(() => {
          this.drawLine_sku();
        });
      } else if (name == 1) {
        this.$nextTick(() => {
          this.drawLine_jjxsku();
        });
      } else if (name == 2) {
        this.$nextTick(() => {
          this.drawLine_yrycsku();
        });
      }
    },
    get_defaultData() {
      this.getStoreInfo = this.$store.getters.getStoreInfo; //获取当前选择的门店信息
      // console.log(this.$store.state.defaultDate);
      const time1 = this.formatDate.pastDate(
        this.$store.state.defaultDate + 1,
        "-"
      );
      const time2 = this.formatDate.pastDate(2, "-");
      this.calendar_startDate = time1; //日历默认开始时间
      this.calendar_endDate = time2; //日历默认结束时间
      this.calendar.value = [time1.split("-"), time2.split("-")]; //日历默认选中日期
      //日历-输入框站位日期
      this.calendar.display =
        this.formatDate.pastDate(this.$store.state.defaultDate + 1, "-") +
        " ~ " +
        this.formatDate.pastDate(2, "-");

      this.calendar2_startDate = time1; //日历默认开始时间
      this.calendar2_endDate = time2; //日历默认结束时间
      this.calendar2.value = [time1.split("-"), time2.split("-")]; //日历默认选中日期
      //日历-输入框站位日期
      this.calendar2.display =
        this.formatDate.pastDate(this.$store.state.defaultDate + 1, "-") +
        " ~ " +
        this.formatDate.pastDate(2, "-");

      this.calendar3_startDate = time1; //日历默认开始时间
      this.calendar3_endDate = time2; //日历默认结束时间
      this.calendar3.value = [time1.split("-"), time2.split("-")]; //日历默认选中日期
      //日历-输入框站位日期
      this.calendar3.display =
        this.formatDate.pastDate(this.$store.state.defaultDate + 1, "-") +
        " ~ " +
        this.formatDate.pastDate(2, "-");
    },
    // 获取数据
    get_data() {
      this.toast.loading({
        message: "加载中...",
        forbidClick: true,
        loadingType: "spinner",
        duration: 0
      });
      this.dateArray = this.dateArray2 = this.dateArray3 = this.formatDate.dateArray(
        this.calendar_startDate,
        this.calendar_endDate
      );
      let params = {
        storeId: this.getStoreInfo.gid,
        startDate: this.calendar_startDate,
        endDate: this.calendar_endDate
      };
      this.serverHost
        .fetchPost("/getReportData", params)
        .then(res => {
          this.toast.clear();
          // console.log(res);
          const resultData = res.data.data;
          //SKU
          const totalSku = [];
          const newDaily = [];
          const everyDaySku = [];
          const dailyDecrease = [];

          //季节性SKU
          const seasonalA = [];
          const seasonalB = [];
          const seasonalC = [];
          const seasonalD = [];
          const foundationSKU = [];

          //移入移出SKU
          const moveInSKU = [];
          const moveOutSKU = [];

          resultData.forEach(item => {
            //SKU
            totalSku.push(item.totalSku);
            newDaily.push(item.newDaily);
            everyDaySku.push(item.everyDaySku);
            dailyDecrease.push(item.dailyDecrease);

            //季节性SKU
            seasonalA.push(item.seasonalA);
            seasonalB.push(item.seasonalB);
            seasonalC.push(item.seasonalC);
            seasonalD.push(item.seasonalD);
            foundationSKU.push(item.foundationSKU);

            //移入移出SKU
            moveInSKU.push(item.moveInSKU);
            moveOutSKU.push(item.moveOutSKU);
          });
          this.resultData = resultData;

          //SKU
          this.totalSku = totalSku;
          this.newDaily = newDaily;
          this.everyDaySku = everyDaySku;
          this.dailyDecrease = dailyDecrease;
          this.drawLine_sku();

          //季节性SKU
          this.seasonalA = seasonalA;
          this.seasonalB = seasonalB;
          this.seasonalC = seasonalC;
          this.seasonalD = seasonalD;
          this.foundationSKU = foundationSKU;

          //移入移出SKU
          this.moveInSKU = moveInSKU;
          this.moveOutSKU = moveOutSKU;
        })
        .catch(res => {
          this.toast.clear();
          console.log(res);
        });
    },
    get_SkuChart() {
      this.toast.loading({
        message: "加载中...",
        forbidClick: true,
        loadingType: "spinner",
        duration: 0
      });
      this.dateArray = this.formatDate.dateArray(
        this.calendar_startDate,
        this.calendar_endDate
      );
      let params = {
        storeId: this.getStoreInfo.gid,
        startDate: this.calendar_startDate,
        endDate: this.calendar_endDate
      };
      this.serverHost
        .fetchPost("/getReportData", params)
        .then(res => {
          this.toast.clear();
          // console.log(res);
          const resultData = res.data.data;
          //SKU
          const totalSku = [];
          const newDaily = [];
          const everyDaySku = [];
          const dailyDecrease = [];
          resultData.forEach(item => {
            //SKU
            totalSku.push(item.totalSku);
            newDaily.push(item.newDaily);
            everyDaySku.push(item.everyDaySku);
            dailyDecrease.push(item.dailyDecrease);
          });
          this.resultData = resultData;
          //SKU
          this.totalSku = totalSku;
          this.newDaily = newDaily;
          this.everyDaySku = everyDaySku;
          this.dailyDecrease = dailyDecrease;
          this.drawLine_sku();
        })
        .catch(res => {
          this.toast.clear();
          console.log(res);
        });
    },
    get_jjxSkuChart() {
      this.toast.loading({
        message: "加载中...",
        forbidClick: true,
        loadingType: "spinner",
        duration: 0
      });
      this.dateArray2 = this.formatDate.dateArray(
        this.calendar2_startDate,
        this.calendar2_endDate
      );
      let params = {
        storeId: this.getStoreInfo.gid,
        startDate: this.calendar2_startDate,
        endDate: this.calendar2_endDate
      };
      this.serverHost
        .fetchPost("/getReportData", params)
        .then(res => {
          this.toast.clear();
          // console.log(res);
          const resultData = res.data.data;
          const seasonalA = [];
          const seasonalB = [];
          const seasonalC = [];
          const seasonalD = [];
          const foundationSKU = [];
          resultData.forEach(item => {
            seasonalA.push(item.seasonalA);
            seasonalB.push(item.seasonalB);
            seasonalC.push(item.seasonalC);
            seasonalD.push(item.seasonalD);
            foundationSKU.push(item.foundationSKU);
          });
          this.resultData = resultData;
          this.seasonalA = seasonalA;
          this.seasonalB = seasonalB;
          this.seasonalC = seasonalC;
          this.seasonalD = seasonalD;
          this.foundationSKU = foundationSKU;
          this.drawLine_jjxsku();
        })
        .catch(res => {
          this.toast.clear();
          console.log(res);
        });
    },
    get_yrycSkuChart() {
      this.toast.loading({
        message: "加载中...",
        forbidClick: true,
        loadingType: "spinner",
        duration: 0
      });
      this.dateArray3 = this.formatDate.dateArray(
        this.calendar3_startDate,
        this.calendar3_endDate
      );
      let params = {
        storeId: this.getStoreInfo.gid,
        startDate: this.calendar3_startDate,
        endDate: this.calendar3_endDate
      };
      this.serverHost
        .fetchPost("/getReportData", params)
        .then(res => {
          this.toast.clear();
          // console.log(res);
          const resultData = res.data.data;
          const moveInSKU = [];
          const moveOutSKU = [];
          resultData.forEach(item => {
            moveInSKU.push(item.moveInSKU);
            moveOutSKU.push(item.moveOutSKU);
          });
          this.resultData = resultData;
          this.moveInSKU = moveInSKU;
          this.moveOutSKU = moveOutSKU;
          this.drawLine_yrycsku();
        })
        .catch(res => {
          this.toast.clear();
          console.log(res);
        });
    },
    openByDialog() {
      this.calendar.show = true;
      this.calendar2.show = true;
      this.calendar3.show = true;
    },
    closeByDialog() {
      this.calendar.show = false;
      this.calendar2.show = false;
      this.calendar3.show = false;
    },
    //画图
    drawLine_sku() {
      // 基于准备好的dom，初始化echarts实例
      let sku_myChart = echarts.init(this.$refs.myChart_sku);
      let dateArray = this.dateArray;
      let totalSku = this.totalSku;
      let newDaily = this.newDaily;
      let everyDaySku = this.everyDaySku;
      let dailyDecrease = this.dailyDecrease;
      // 绘制图表
      sku_myChart.setOption({
        //鼠标移入图标提示层
        tooltip: {
          trigger: "axis"
        },
        //图例组件相关的行为，必须引入图例组件后才能使用。
        legend: {
          data: ["总SKU", "每日新增", "每日在售", "每日减少"],
          bottom: 0,
          left: "center"
        },
        //图标位置
        grid: {
          top: "5%",
          left: "3%",
          right: "12%",
          bottom: "10%",
          containLabel: true
        },
        //图标 x 轴
        xAxis: {
          type: "category",
          boundaryGap: false,
          data: dateArray
        },
        //图标 y 轴
        yAxis: {
          type: "value"
        },
        //数据
        series: [
          {
            name: "总SKU",
            type: "line",
            data: totalSku
          },
          {
            name: "每日新增",
            type: "line",
            data: newDaily
          },
          {
            name: "每日在售",
            type: "line",
            data: everyDaySku
          },
          {
            name: "每日减少",
            type: "line",
            data: dailyDecrease
          }
        ]
      });
    },
    drawLine_jjxsku() {
      // 基于准备好的dom，初始化echarts实例
      let sku_myChart = echarts.init(this.$refs.myChart_jjxSku);
      let dateArray2 = this.dateArray2;
      let seasonalA = this.seasonalA;
      let seasonalB = this.seasonalB;
      let seasonalC = this.seasonalC;
      let seasonalD = this.seasonalD;
      let foundationSKU = this.foundationSKU;
      // 绘制图表
      sku_myChart.setOption({
        //鼠标移入图标提示层
        tooltip: {
          trigger: "axis"
        },
        //图例组件相关的行为，必须引入图例组件后才能使用。
        legend: {
          data: ["春季SKU", "夏季SKU", "秋季SKU", "冬季SKU", "基础SKU"],
          bottom: 0,
          left: "center"
        },
        //图标位置
        grid: {
          top: "5%",
          left: "3%",
          right: "12%",
          bottom: "16%",
          containLabel: true
        },
        //图标 x 轴
        xAxis: {
          type: "category",
          boundaryGap: false,
          data: dateArray2
        },
        //图标 y 轴
        yAxis: {
          type: "value"
        },
        //数据
        series: [
          {
            name: "春季SKU",
            type: "line",
            data: seasonalA
          },
          {
            name: "夏季SKU",
            type: "line",
            data: seasonalB
          },
          {
            name: "秋季SKU",
            type: "line",
            data: seasonalC
          },
          {
            name: "冬季SKU",
            type: "line",
            data: seasonalD
          },
          {
            name: "基础SKU",
            type: "line",
            data: foundationSKU
          }
        ]
      });
    },
    drawLine_yrycsku() {
      // 基于准备好的dom，初始化echarts实例
      let sku_myChart = echarts.init(this.$refs.myChart_yrycSku);
      let dateArray3 = this.dateArray3;
      let moveInSKU = this.moveInSKU;
      let moveOutSKU = this.moveOutSKU;
      // 绘制图表
      sku_myChart.setOption({
        //鼠标移入图标提示层
        tooltip: {
          trigger: "axis"
        },
        //图例组件相关的行为，必须引入图例组件后才能使用。
        legend: {
          data: ["移入SKU", "移出SKU"],
          bottom: 0,
          left: "center"
        },
        //图标位置
        grid: {
          top: "5%",
          left: "3%",
          right: "12%",
          bottom: "10%",
          containLabel: true
        },
        //图标 x 轴
        xAxis: {
          type: "category",
          boundaryGap: false,
          data: dateArray3
        },
        //图标 y 轴
        yAxis: {
          type: "value"
        },
        //数据
        series: [
          {
            name: "移入SKU",
            type: "line",
            data: moveInSKU
          },
          {
            name: "移出SKU",
            type: "line",
            data: moveOutSKU
          }
        ]
      });
    }
  }
};
</script>

<style scoped lang='scss'>
.chartPage {
  min-height: 100vh;
}
.myChart-box {
  padding: 15px 0;
}
</style>
